import { mapState, mapActions } from 'vuex'

export default {
  data () {
    return {
      wordVal: ''
    }
  },
  computed: {
    ...mapState([
      'userInfo'
    ])
  },
  methods: {
    ...mapActions([
      'addContentWord'
    ]),
    // 跳转关键词列表
    onSkipPageFn (url) {
      uni.navigateTo({
        url
      })
    },
    // 提交
    onSubmitFn () {
      const data = {
        that: this,
        wordVal: this.wordVal
      }
      this.addContentWord(data).then((res) => {
        this.showToastFn(res.message)
        if (res.ret === 1) {
          this.wordVal = ''
        }
      })
    },
    showToastFn (title) {
      uni.showToast({
        title,
        icon: 'none',
        duration: 2000
      })
    }
  }
}
